import getCanvasSize from '../canvasSize/getCanvasSize';
import clearCanvas from './utils/clearCanvas';

export default function move(e) {
  let isDrag = true;
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');
  const imageData = ctx.getImageData(0, 0, getCanvasSize(), getCanvasSize());
  const x = Math.floor(e.offsetX * getCanvasSize() / canvas.offsetWidth);
  const y = Math.floor(e.offsetY * getCanvasSize() / canvas.offsetHeight);

  function mousemoveEvent(event) {
    if (isDrag) {
      const x1 = Math.floor(event.offsetX * getCanvasSize() / canvas.offsetWidth);
      const y1 = Math.floor(event.offsetY * getCanvasSize() / canvas.offsetHeight);

      clearCanvas(canvas);

      ctx.putImageData(imageData, x1 - x, y1 - y);
    }
  }

  function mouseupEvent() {
    isDrag = false;
    canvas.removeEventListener('mousemove', mousemoveEvent);
    canvas.removeEventListener('mouseup', mouseupEvent);
    canvas.removeEventListener('mouseleave', mouseupEvent);
  }

  if (e.which === 1 || e.which === 3) {
    canvas.addEventListener('mousemove', mousemoveEvent);
    canvas.addEventListener('mouseup', mouseupEvent);
    canvas.addEventListener('mouseleave', mouseupEvent);
  }
}
