const shortcuts = {
  Pen: {
    code: 80,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Eraser: {
    code: 69,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  PaintAllPixelsOfSameColor: {
    code: 65,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Rectangle: {
    code: 82,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Stroke: {
    code: 83,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Circle: {
    code: 67,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Lighten: {
    code: 85,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Dithering: {
    code: 84,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Move: {
    code: 77,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  VerticalMirrorPen: {
    code: 86,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  ColorPicker: {
    code: 79,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Rotate: {
    code: 75,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Flip: {
    code: 70,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
  Negative: {
    code: 78,
    ctrlKey: false,
    altKey: false,
    shiftKey: false,
  },
};

function eventForClickShortcut(e) {
  let { target } = e;
  const activeShortkut = document.querySelector('.shortcut.active');

  while (target.className !== 'shortcut' && target.className !== 'shortcut-container') {
    target = target.parentNode;
  }

  if (target.className === 'shortcut' && !activeShortkut) {
    target.classList.add('active');
  }
}

function eventForChangeShortcut(e) {
  const activeShortcut = document.querySelector('.shortcut.active');

  if (activeShortcut && !Object.keys(shortcuts).some(key => (shortcuts[key].code === e.keyCode
    && shortcuts[key].ctrlKey === e.ctrlKey && shortcuts[key].altKey === e.altKey
    && shortcuts[key].shiftKey === e.shiftKey))) {
    if (e.keyCode >= 65 && e.keyCode <= 90) {
      let newShortcut = '';

      if (e.ctrlKey) {
        newShortcut += 'Ctrl + ';
        shortcuts[activeShortcut.getAttribute('value')].ctrlKey = true;
      }

      if (e.altKey) {
        newShortcut += 'Alt + ';
        shortcuts[activeShortcut.getAttribute('value')].altKey = true;
      }

      if (e.shiftKey) {
        newShortcut += 'Shift + ';
        shortcuts[activeShortcut.getAttribute('value')].shiftKey = true;
      }

      shortcuts[activeShortcut.getAttribute('value')].code = e.keyCode;

      newShortcut += e.key.toUpperCase();
      activeShortcut.querySelector('.shortcut-block').innerHTML = newShortcut;
      activeShortcut.classList.remove('active');
    }
  }
}

export { shortcuts, eventForClickShortcut, eventForChangeShortcut };
