export default class ExportHTMLStructure {
  static renderButtonForExportGif() {
    const exportContainer = document.createElement('div');
    exportContainer.classList.add('export-container');
    document.querySelector('.export-field').appendChild(exportContainer);

    const buttonExportGif = document.createElement('button');
    buttonExportGif.classList.add('export-gif-in-local');
    buttonExportGif.innerHTML = 'GIF';
    exportContainer.appendChild(buttonExportGif);
  }

  static renderButtonForExportAPNG() {
    const buttonExportAPNG = document.createElement('button');
    buttonExportAPNG.classList.add('export-apng-in-local');
    buttonExportAPNG.innerHTML = 'APNG';
    document.querySelector('.export-container').appendChild(buttonExportAPNG);
  }

  static renderButtonForUploadToGoogleDrive() {
    const buttonForUploadToGoogleDrive = document.createElement('button');
    buttonForUploadToGoogleDrive.classList.add('export-apng-to-google-drive');
    buttonForUploadToGoogleDrive.innerHTML = 'Upload to google drive';
    document.querySelector('.export-container').appendChild(buttonForUploadToGoogleDrive);
  }

  static renderimportPiskel() {
    const importPiskel = document.createElement('input');
    importPiskel.classList.add('import-piskel');
    importPiskel.setAttribute('type', 'file');
    importPiskel.setAttribute('id', 'import-piskel');
    document.querySelector('.export-field').appendChild(importPiskel);

    const labelForButtonForImportPiskel = document.createElement('label');
    labelForButtonForImportPiskel.classList.add('label-import-piskel');
    labelForButtonForImportPiskel.setAttribute('for', 'import-piskel');
    labelForButtonForImportPiskel.innerHTML = 'Import .piskel';
    document.querySelector('.export-field').appendChild(labelForButtonForImportPiskel);
  }

  static renderButtonForUloadPiskel() {
    const buttonForUploadPiskel = document.createElement('button');
    buttonForUploadPiskel.classList.add('export-to-piskel');
    buttonForUploadPiskel.innerHTML = 'Save .piskel';
    document.querySelector('.export-container').appendChild(buttonForUploadPiskel);
  }

  static render() {
    ExportHTMLStructure.renderButtonForExportGif();
    ExportHTMLStructure.renderButtonForExportAPNG();
    ExportHTMLStructure.renderButtonForUploadToGoogleDrive();
    ExportHTMLStructure.renderButtonForUloadPiskel();
    ExportHTMLStructure.renderimportPiskel();
  }
}
