import exportGIFtoLocalSystem from '../export/exportGIFtoLocalSystem';
import exportAPNGtoLocalSystem from '../export/exportAPNGtoLocalSystem';
import exportAPNGtoGoogleDrive from '../export/exportAPNGtoGoogleDrive';
import exportToPiskel from '../export/exportToPiskel';

export default function ExportController() {
  document.querySelector('.export-gif-in-local').addEventListener('click', exportGIFtoLocalSystem);
  document.querySelector('.export-apng-in-local').addEventListener('click', exportAPNGtoLocalSystem);
  document.querySelector('.export-apng-to-google-drive').addEventListener('click', exportAPNGtoGoogleDrive);
  document.querySelector('.export-to-piskel').addEventListener('click', exportToPiskel);
}
