export default class ToolsHTMLStructure {
  static colors() {
    const wrapperColors = document.createElement('div');
    wrapperColors.classList.add('colors-wrapper');

    const primaryColor = document.createElement('input');
    primaryColor.setAttribute('type', 'color');
    primaryColor.setAttribute('id', 'primary-color');
    primaryColor.setAttribute('name', 'primary-color');
    primaryColor.setAttribute('value', '#FF0000');
    wrapperColors.appendChild(primaryColor);

    const secondaryColor = document.createElement('input');
    secondaryColor.setAttribute('type', 'color');
    secondaryColor.setAttribute('id', 'secondary-color');
    secondaryColor.setAttribute('name', 'secondary-color');
    secondaryColor.setAttribute('value', '#FFFFFF');
    wrapperColors.appendChild(secondaryColor);

    document.querySelector('.tools').appendChild(wrapperColors);
  }

  static buttonWrapper() {
    const buttonWrapper = document.createElement('div');
    buttonWrapper.classList.add('button-wrapper');

    document.querySelector('.tools').appendChild(buttonWrapper);
  }

  static renderButton(buttonClass, value) {
    const button = document.createElement('button');
    button.classList.add('button-tool');
    button.classList.add(buttonClass);
    button.setAttribute('value', value);

    document.querySelector('.button-wrapper').appendChild(button);
  }

  static render() {
    ToolsHTMLStructure.buttonWrapper();
    ToolsHTMLStructure.renderButton('button-pen', 'Pen');
    ToolsHTMLStructure.renderButton('button-eraser', 'Eraser');
    ToolsHTMLStructure.renderButton('button-paint-all-pixels-of-same-color', 'PaintAllPixelsOfSameColor');
    ToolsHTMLStructure.renderButton('button-stroke', 'Stroke');
    ToolsHTMLStructure.renderButton('button-rectangle', 'Rectangle');
    ToolsHTMLStructure.renderButton('button-circle', 'Circle');
    ToolsHTMLStructure.renderButton('button-lighten', 'Lighten');
    ToolsHTMLStructure.renderButton('button-dithering', 'Dithering');
    ToolsHTMLStructure.renderButton('button-move', 'Move');
    ToolsHTMLStructure.renderButton('button-vertical-mirror-pen', 'VerticalMirrorPen');
    ToolsHTMLStructure.renderButton('button-color-picker', 'ColorPicker');
    ToolsHTMLStructure.renderButton('button-rotate', 'Rotate');
    ToolsHTMLStructure.renderButton('button-flip', 'Flip');
    ToolsHTMLStructure.renderButton('button-negative', 'Negative');
    ToolsHTMLStructure.colors();
  }
}
