import simplePen from '../tools/simplePen';
import eraser from '../tools/eraser';
import paintAllPixelsOfSameColor from '../tools/paintAllPixelsOfSameColor';
import rectangle from '../tools/rectangle';
import stroke from '../tools/stroke';
import circle from '../tools/circle';
import lighten from '../tools/lighten';
import dithering from '../tools/dithering';
import move from '../tools/move';
import verticalMirrorPen from '../tools/verticalMirrorPen';
import colorPicker from '../tools/colorPicker';
import rotate from '../tools/rotate';
import flip from '../tools/flip';
import negative from '../tools/negative';

import { shortcuts } from '../modal/eventForChangeShortcut';

export default function toolsController() {
  const tools = {
    currentTool: '',
    prevTool: '',
    Pen: simplePen,
    Eraser: eraser,
    PaintAllPixelsOfSameColor: paintAllPixelsOfSameColor,
    Rectangle: rectangle,
    Stroke: stroke,
    Circle: circle,
    Lighten: lighten,
    Dithering: dithering,
    Move: move,
    VerticalMirrorPen: verticalMirrorPen,
    ColorPicker: colorPicker,
    Rotate: rotate,
    Flip: flip,
    Negative: negative,
  };

  const buttonWrapper = document.querySelector('.button-wrapper');
  const canvasWrapper = document.querySelector('.canvas-wrapper');
  buttonWrapper.addEventListener('click', (e) => {
    const { target } = e;

    if (target.nodeName === 'BUTTON') {
      if (tools.currentTool !== '') {
        canvasWrapper.removeEventListener('mousedown', tools.currentTool);
        document.querySelector(`button[value='${tools.prevTool}']`).classList.remove('active-tool');
      }
      tools.currentTool = tools[target.value];
      tools.prevTool = target.value;

      canvasWrapper.addEventListener('mousedown', tools.currentTool);
      document.querySelector(`button[value='${tools.prevTool}']`).classList.add('active-tool');
    }
  });

  document.addEventListener('keydown', (e) => {
    if (!document.querySelector('.shortcut.active')) {
      // eslint-disable-next-line no-restricted-syntax
      for (const key in shortcuts) {
        if (e.keyCode === shortcuts[key].code && e.ctrlKey === shortcuts[key].ctrlKey
          && e.altKey === shortcuts[key].altKey && e.shiftKey === shortcuts[key].shiftKey) {
          if (tools.currentTool !== '') {
            canvasWrapper.removeEventListener('mousedown', tools.currentTool);
            document.querySelector(`button[value='${tools.prevTool}']`).classList.remove('active-tool');
          }
          tools.currentTool = tools[key];
          tools.prevTool = key;

          canvasWrapper.addEventListener('mousedown', tools.currentTool);
          document.querySelector(`button[value='${tools.prevTool}']`).classList.add('active-tool');
        }
      }
    }
  });
}
