import download from 'downloadjs';
import UPNG from 'upng-js';
import getCurrentFPS from '../preview/getCurrentFPS';
import getCanvasSize from '../canvasSize/getCanvasSize';

export default function exportAPNGtoLocalSystem() {
  const framesCanvas = Array.from(document.querySelectorAll('.frame-canvas'));

  const framesCanvasBuffer = framesCanvas.map(item => item.getContext('2d').getImageData(0, 0, getCanvasSize(), getCanvasSize()).data.buffer);
  const arrayFPS = new Array(framesCanvasBuffer.length).fill(1000 / getCurrentFPS());

  const apng = UPNG.encode(framesCanvasBuffer, getCanvasSize(), getCanvasSize(), 0, arrayFPS);

  download(apng, 'apng.apng', 'image/apng');
}
