export default class ModalWindow {
  static renderModalWindow() {
    const modal = document.createElement('div');
    modal.classList.add('modal');
    document.querySelector('.header').appendChild(modal);

    const cross = document.createElement('div');
    cross.classList.add('modal__cross');
    modal.appendChild(cross);

    const overlay = document.createElement('div');
    overlay.classList.add('overlay');
    overlay.setAttribute('id', 'overlay-modal');
    document.querySelector('.header').appendChild(overlay);
  }

  static renderShortkutContainer() {
    const ul = document.createElement('ul');
    ul.classList.add('shortcut-container');
    document.querySelector('.modal').appendChild(ul);
  }

  static renderShorkut(letter, name, value) {
    const li = document.createElement('li');
    li.classList.add('shortcut');
    li.setAttribute('value', value);
    document.querySelector('.shortcut-container').appendChild(li);

    const div = document.createElement('div');
    div.classList.add('shortcut-block');
    div.innerHTML = letter;
    li.appendChild(div);

    const p = document.createElement('p');
    p.classList.add('shortcut-name');
    p.innerHTML = name;
    li.appendChild(p);
  }

  static render() {
    ModalWindow.renderModalWindow();
    ModalWindow.renderShortkutContainer();
    ModalWindow.renderShorkut('P', 'Pen tool', 'Pen');
    ModalWindow.renderShorkut('V', 'Vertical mirror pen tool', 'VerticalMirrorPen');
    ModalWindow.renderShorkut('A', 'Magic bucket tool', 'PaintAllPixelsOfSameColor');
    ModalWindow.renderShorkut('E', 'Eraser tool', 'Eraser');
    ModalWindow.renderShorkut('S', 'Stroke tool', 'Stroke');
    ModalWindow.renderShorkut('R', 'Rectangle tool', 'Rectangle');
    ModalWindow.renderShorkut('C', 'Circle tool', 'Circle');
    ModalWindow.renderShorkut('M', 'Move tool', 'Move');
    ModalWindow.renderShorkut('U', 'Lighten tool', 'Lighten');
    ModalWindow.renderShorkut('T', 'Dithering tool', 'Dithering');
    ModalWindow.renderShorkut('K', 'Rotate tool', 'Rotate');
    ModalWindow.renderShorkut('F', 'Flip tool', 'Flip');
    ModalWindow.renderShorkut('N', 'Negative tool', 'Negative');
    ModalWindow.renderShorkut('O', 'Color Picker tool', 'ColorPicker');
  }
}
