import getCanvasSize from '../../canvasSize/getCanvasSize';
import FramesHTMLStructure from '../../rendering/FramesField';

export default class InteractionCanvasAndFrame {
  static eventMouseUpCanvas() {
    const currentSelectedFrame = document.querySelector('.selected-frame');
    const ctxFrameCanvas = currentSelectedFrame.querySelector('.frame-canvas').getContext('2d');

    const mainCanvas = document.getElementById('canvas');
    const ctxMainCanvas = mainCanvas.getContext('2d');

    const picture = ctxMainCanvas.getImageData(0, 0, getCanvasSize(), getCanvasSize());

    ctxFrameCanvas.putImageData(picture, 0, 0);
  }

  static eventAddNewFrame() {
    document.querySelector('.selected-frame').classList.remove('selected-frame');
    document.querySelector('.button-delete').style.display = 'block';

    FramesHTMLStructure.renderFrame(getCanvasSize());

    const ctxMainCanvas = document.getElementById('canvas').getContext('2d');
    const currentPicture = ctxMainCanvas.getImageData(0, 0, getCanvasSize(), getCanvasSize());
    const emptyPicture = ctxMainCanvas.createImageData(currentPicture);
    ctxMainCanvas.putImageData(emptyPicture, 0, 0);
  }

  static eventClickOnFrame(e) {
    const { target } = e;

    if (target.className === 'frame-canvas') {
      document.querySelector('.selected-frame').classList.remove('selected-frame');

      const ctxCurrent = target.getContext('2d');
      const picture = ctxCurrent.getImageData(0, 0, getCanvasSize(), getCanvasSize());

      const ctxMainCanvas = document.getElementById('canvas').getContext('2d');
      ctxMainCanvas.putImageData(picture, 0, 0);

      target.parentNode.parentNode.classList.add('selected-frame');
    }
  }
}
