export default class HeaderHTMLStructure {
  static signIn() {
    const oauthContainer = document.createElement('div');
    oauthContainer.classList.add('header-button-container');

    document.querySelector('.header').appendChild(oauthContainer);

    const buttonSignIn = document.createElement('div');
    buttonSignIn.classList.add('g-signin2');
    buttonSignIn.setAttribute('data-onsuccess', 'onSignIn');
    buttonSignIn.setAttribute('data-theme', 'dark');

    oauthContainer.appendChild(buttonSignIn);
  }

  static logOut() {
    const buttonLogOut = document.createElement('button');
    buttonLogOut.classList.add('button-log-out');
    buttonLogOut.innerHTML = 'Log out';
    document.querySelector('.header-button-container').appendChild(buttonLogOut);
  }

  static hotkeys() {
    const hotkeys = document.createElement('button');
    hotkeys.classList.add('hotkeys');

    document.querySelector('.header').appendChild(hotkeys);
  }

  static render() {
    HeaderHTMLStructure.signIn();
    HeaderHTMLStructure.hotkeys();
    HeaderHTMLStructure.logOut();
  }
}
