import getCanvasSize from '../canvasSize/getCanvasSize';
import drawLine from './utils/drawLine';
import setPixel from './utils/setPixel';

export default function eraser(e) {
  let isDrag = true;
  const rgbColor = {
    r: 0,
    g: 0,
    b: 0,
    a: 0,
  };

  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');

  let x = Math.floor(e.offsetX * getCanvasSize() / canvas.offsetWidth);
  let y = Math.floor(e.offsetY * getCanvasSize() / canvas.offsetHeight);
  let x1;
  let y1;

  function mousemoveEvent(event) {
    if (isDrag) {
      x1 = Math.floor(event.offsetX * getCanvasSize() / canvas.offsetWidth);
      y1 = Math.floor(event.offsetY * getCanvasSize() / canvas.offsetHeight);

      drawLine(x, y, x1, y1, rgbColor, ctx);

      x = x1;
      y = y1;
    }
  }

  function mouseupEvent() {
    isDrag = false;
    canvas.removeEventListener('mousemove', mousemoveEvent);
    canvas.removeEventListener('mouseup', mouseupEvent);
    canvas.removeEventListener('mouseleave', mouseupEvent);
  }

  setPixel(rgbColor, x, y, ctx);

  canvas.addEventListener('mousemove', mousemoveEvent);
  canvas.addEventListener('mouseup', mouseupEvent);
  canvas.addEventListener('mouseleave', mouseupEvent);
}
