import hexToRgba from './utils/hexToRgba';
import getCanvasSize from '../canvasSize/getCanvasSize';
import drawCircle from './utils/drawCircle';
import clearCanvas from './utils/clearCanvas';

export default function circle(e) {
  let isDrag = true;
  let rgbColor;
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');

  const additionalCanvas = document.getElementById('additional-canvas');
  additionalCanvas.style.zIndex = '10';
  const additionalCtx = additionalCanvas.getContext('2d');

  const x = Math.floor(e.offsetX * getCanvasSize() / additionalCanvas.offsetWidth);
  const y = Math.floor(e.offsetY * getCanvasSize() / additionalCanvas.offsetHeight);
  let radius;

  function mousemoveEvent(event) {
    if (isDrag) {
      const x1 = Math.floor(event.offsetX * getCanvasSize() / additionalCanvas.offsetWidth);
      const y1 = Math.floor(event.offsetY * getCanvasSize() / additionalCanvas.offsetHeight);

      radius = Math.sqrt((x1 - x) * (x1 - x) + (y1 - y) * (y1 - y));

      clearCanvas(additionalCanvas);
      drawCircle(x, y, radius, rgbColor, additionalCtx);
    }
  }

  function mouseupEvent() {
    isDrag = false;
    clearCanvas(additionalCanvas);
    drawCircle(x, y, radius, rgbColor, ctx);

    additionalCanvas.style.zIndex = '-1';
    additionalCanvas.removeEventListener('mousemove', mousemoveEvent);
    additionalCanvas.removeEventListener('mouseup', mouseupEvent);
  }

  if (e.which === 1 || e.which === 3) {
    if (e.which === 1) {
      rgbColor = hexToRgba(document.getElementById('primary-color').value, 255);
    } else if (e.which === 3) {
      rgbColor = hexToRgba(document.getElementById('secondary-color').value, 255);
    }

    additionalCanvas.addEventListener('mousemove', mousemoveEvent);
    additionalCanvas.addEventListener('mouseup', mouseupEvent);
  }
}
