import getCanvasSize from '../canvasSize/getCanvasSize';

export default class EventsFrame {
  static eventDeleteFrame(e) {
    const { target } = e;

    if (target.className === 'button-in-frame button-delete') {
      const collectionFrames = document.querySelectorAll('.frame');

      if (target.parentNode.className === 'frame selected-frame') {
        let picture;
        if (collectionFrames[0] === target.parentNode) {
          target.parentNode.nextElementSibling.classList.add('selected-frame');

          picture = target.parentNode.nextElementSibling.querySelector('.frame-canvas').getContext('2d').getImageData(0, 0, getCanvasSize(), getCanvasSize());
        } else {
          target.parentNode.previousElementSibling.classList.add('selected-frame');

          picture = target.parentNode.previousElementSibling.querySelector('.frame-canvas').getContext('2d').getImageData(0, 0, getCanvasSize(), getCanvasSize());
        }

        document.getElementById('canvas').getContext('2d').putImageData(picture, 0, 0);
      }
      target.parentNode.remove();

      if (collectionFrames.length === 2) {
        document.querySelector('.button-delete').style.display = 'none';
      }
    }
  }

  static eventCopyFrame(e) {
    const { target } = e;

    if (target.className === 'button-in-frame button-copy') {
      const collectionFrames = document.querySelectorAll('.frame');

      if (collectionFrames.length === 1) {
        document.querySelector('.button-delete').style.display = 'block';
      }

      const picture = target.parentNode.querySelector('.frame-canvas').getContext('2d').getImageData(0, 0, getCanvasSize(), getCanvasSize());

      const copyFrame = target.parentNode.cloneNode(true);
      if (copyFrame.className === 'frame selected-frame') {
        copyFrame.classList.remove('selected-frame');
      }

      copyFrame.querySelector('.frame-canvas').getContext('2d').putImageData(picture, 0, 0);

      document.querySelector('.frames-list').insertBefore(copyFrame, target.parentNode.nextElementSibling);
    }
  }
}
