import setPixel from './setPixel';

export default function drawCircle(x, y, radius, color, ctx) {
  let x0 = 0;
  let y0 = radius;
  let gap = 0;
  let delta = (2 - 2 * radius);

  while (y0 >= 0) {
    setPixel(color, x + x0, y - y0, ctx);
    setPixel(color, x - x0, y - y0, ctx);
    setPixel(color, x - x0, y + y0, ctx);
    setPixel(color, x + x0, y + y0, ctx);
    gap = 2 * (delta + y0) - 1;
    if (delta < 0 && gap <= 0) {
      x0 += 1;
      delta += 2 * x0 + 1;
      // eslint-disable-next-line no-continue
      continue;
    }
    if (delta > 0 && gap > 0) {
      y0 -= 1;
      delta -= 2 * y0 + 1;
      // eslint-disable-next-line no-continue
      continue;
    }
    x0 += 1;
    delta += 2 * (x0 - y0);
    y0 -= 1;
  }
}
