export default function changeSize(e) {
  const { target } = e;
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');

  const additionalCanvas = document.getElementById('additional-canvas');

  if (target.value > canvas.getAttribute('width')) {
    const picture = ctx.getImageData(0, 0, canvas.getAttribute('width') - 0, canvas.getAttribute('height') - 0);

    canvas.setAttribute('width', target.value);
    canvas.setAttribute('height', target.value);

    additionalCanvas.setAttribute('width', target.value);
    additionalCanvas.setAttribute('height', target.value);

    ctx.putImageData(picture, 0, 0);
  } else if (target.value < canvas.getAttribute('width')) {
    const picture = ctx.getImageData(0, 0, target.value - 0, target.value - 0);

    canvas.setAttribute('width', target.value);
    canvas.setAttribute('height', target.value);

    additionalCanvas.setAttribute('width', target.value);
    additionalCanvas.setAttribute('height', target.value);

    ctx.putImageData(picture, 0, 0);
  }

  const framesCanvas = document.querySelectorAll('.frame-canvas');
  for (let i = 0; i < framesCanvas.length; i += 1) {
    const ctxFramesCanvas = framesCanvas[i].getContext('2d');

    if (target.value > framesCanvas[i].getAttribute('width')) {
      const picture = ctxFramesCanvas.getImageData(0, 0, framesCanvas[i].getAttribute('width') - 0, framesCanvas[i].getAttribute('height') - 0);
      framesCanvas[i].setAttribute('width', target.value);
      framesCanvas[i].setAttribute('height', target.value);

      ctxFramesCanvas.putImageData(picture, 0, 0);
    } else if (target.value < framesCanvas[i].getAttribute('width')) {
      const picture = ctxFramesCanvas.getImageData(0, 0, target.value - 0, target.value - 0);
      framesCanvas[i].setAttribute('width', target.value);
      framesCanvas[i].setAttribute('height', target.value);

      ctxFramesCanvas.putImageData(picture, 0, 0);
    }
  }

  const previewCanvas = document.querySelector('.canvas-frame-container');
  if (target.value > previewCanvas.getAttribute('width')) {
    previewCanvas.setAttribute('width', target.value);
    previewCanvas.setAttribute('height', target.value);
  } else if (target.value < previewCanvas.getAttribute('width')) {
    previewCanvas.setAttribute('width', target.value);
    previewCanvas.setAttribute('height', target.value);
  }

  const canvasSize = document.querySelector('.canvas-size');
  canvasSize.innerHTML = `[${canvas.width}x${canvas.height}]`;
}
