import eventForLoad from '../oauth/eventForLoad';
import eventForSignIn from '../oauth/eventForSignIn';
import eventForLogOut from '../oauth/eventForLogOut';
import init from '../oauth/globalFunctionForInitOauth';

export default function OAuthController() {
  document.addEventListener('DOMContentLoaded', eventForLoad);
  document.querySelector('.g-signin2').addEventListener('click', eventForSignIn);
  document.querySelector('.button-log-out').addEventListener('click', eventForLogOut);
  window.init = init;
}
