export default function signIn() {
  const auth2 = window.gapi.auth2.getAuthInstance();
  auth2.signIn().then((googleUser) => {
    const profile = googleUser.getBasicProfile();

    if (!localStorage.getItem('greeting')) {
      const p = document.createElement('p');
      p.classList.add('greeting');
      p.innerHTML = `Hello, ${profile.getName()}`;
      document.querySelector('.header').insertBefore(p, document.querySelector('.hotkeys'));
    }

    document.querySelector('.button-log-out').style.display = 'block';
    localStorage.setItem('greeting', `Hello, ${profile.getName()}`);
  });
}
