import FramesHTMLStructure from '../rendering/FramesField';

async function transformLayers(layers, size) {
  const resultLayers = [];
  const frames = document.querySelectorAll('.frame-canvas');
  for (let i = 0; i < layers.length; i += 1) {
    const canvasCommon = document.createElement('canvas');
    canvasCommon.setAttribute('width', size * JSON.parse(layers[0]).frameCount);
    canvasCommon.setAttribute('height', size);
    const ctxCommon = canvasCommon.getContext('2d');
    const img = new Image();
    img.src = JSON.parse(layers[i]).chunks[0].base64PNG;
    img.onload = () => {
      ctxCommon.drawImage(img, 0, 0);

      for (let j = 0; j < frames.length; j += 1) {
        const oneCanvas = document.createElement('canvas');
        oneCanvas.setAttribute('width', size);
        oneCanvas.setAttribute('height', size);
        const oneCtx = oneCanvas.getContext('2d');

        const currentPicture = ctxCommon.getImageData(size * j, 0, size, size);
        oneCtx.putImageData(currentPicture, 0, 0);
        const urlCurrentPicture = oneCanvas.toDataURL();
        const image = new Image();

        image.onload = () => {
          frames[j].getContext('2d').drawImage(image, 0, 0);
          const picture = frames[j].getContext('2d').getImageData(0, 0, size, size);
          document.getElementById('canvas').getContext('2d').putImageData(picture, 0, 0);
        };

        image.src = urlCurrentPicture;
      }
    };

    resultLayers.push(canvasCommon);
  }
  return resultLayers;
}

export default function importPiskel() {
  const reader = new FileReader();

  reader.readAsText(document.querySelector('.import-piskel').files[0]);

  reader.onload = async function readerOnload(event) {
    const contents = JSON.parse(event.target.result);

    document.getElementById(`canvas-size-${contents.piskel.width}`).click();

    const framesCanvas = Array.from(document.querySelectorAll('.frame'));
    framesCanvas.forEach((item) => {
      item.remove();
    });

    for (let i = 0; i < JSON.parse(contents.piskel.layers[0]).frameCount; i += 1) {
      FramesHTMLStructure.renderFrame(contents.piskel.width);

      if (JSON.parse(contents.piskel.layers[0]).frameCount === 1) {
        FramesHTMLStructure.addDisplayNoneForFirstFrame();
      }

      if (i !== 0) {
        document.querySelector('.selected-frame').classList.remove('selected-frame');
      }
    }
    transformLayers(contents.piskel.layers, contents.piskel.width);
  };
}
