import UPNG from 'upng-js';
import getCurrentFPS from '../preview/getCurrentFPS';
import getCanvasSize from '../canvasSize/getCanvasSize';

export default async function exportAPNGtoGoogleDrive() {
  const framesCanvas = Array.from(document.querySelectorAll('.frame-canvas'));
  const framesCanvasBuffer = framesCanvas.map(item => item.getContext('2d').getImageData(0, 0, getCanvasSize(), getCanvasSize()).data.buffer);
  const arrayFPS = new Array(framesCanvasBuffer.length).fill(1000 / getCurrentFPS());

  const apng = UPNG.encode(framesCanvasBuffer, getCanvasSize(), getCanvasSize(), 0, arrayFPS);

  const file = new Blob([apng], { type: 'image/apng' });
  const metadata = {
    name: 'sampleName.apng',
    mimeType: 'image/apng',
  };

  const accessToken = window.gapi.auth.getToken().access_token;
  const form = new FormData();
  form.append('metadata', new Blob([JSON.stringify(metadata)], { type: 'application/json' }));
  form.append('file', file);

  fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart&fields=id', {
    method: 'POST',
    headers: new Headers({ Authorization: `Bearer ${accessToken}` }),
    body: form,
  }).then(res => res.json()).then((val) => {
    window.open(`https://drive.google.com/file/d/${val.id}/view`);
  });
}
