import changeCanvas from '../canvasSize/changeCanvasSize';

export default class CanvasHTMLStructure {
  static renderMainCanvas() {
    const canvasWrapper = document.createElement('div');
    canvasWrapper.classList.add('canvas-wrapper');

    const canvas = document.createElement('canvas');
    canvas.setAttribute('id', 'canvas');
    canvas.setAttribute('width', '32');
    canvas.setAttribute('height', '32');
    canvas.addEventListener('contextmenu', e => e.preventDefault());
    canvasWrapper.appendChild(canvas);

    document.querySelector('.canvas-field').appendChild(canvasWrapper);
  }

  static renderChooseSizeCanvas() {
    const wrapperRadio = document.createElement('div');
    wrapperRadio.classList.add('radio-wrapper');

    const radio32px = document.createElement('input');
    radio32px.setAttribute('type', 'radio');
    radio32px.setAttribute('id', 'canvas-size-32');
    radio32px.setAttribute('name', 'canvas-size');
    radio32px.setAttribute('value', '32');
    radio32px.setAttribute('checked', 'true');
    wrapperRadio.appendChild(radio32px);

    const label32px = document.createElement('label');
    label32px.setAttribute('for', 'canvas-size-32');
    label32px.innerHTML = '32px';
    wrapperRadio.appendChild(label32px);

    const radio64px = document.createElement('input');
    radio64px.setAttribute('type', 'radio');
    radio64px.setAttribute('id', 'canvas-size-64');
    radio64px.setAttribute('name', 'canvas-size');
    radio64px.setAttribute('value', '64');
    wrapperRadio.appendChild(radio64px);

    const label64px = document.createElement('label');
    label64px.setAttribute('for', 'canvas-size-64');
    label64px.innerHTML = '64px';
    wrapperRadio.appendChild(label64px);

    const radio128px = document.createElement('input');
    radio128px.setAttribute('type', 'radio');
    radio128px.setAttribute('id', 'canvas-size-128');
    radio128px.setAttribute('name', 'canvas-size');
    radio128px.setAttribute('value', '128');
    wrapperRadio.appendChild(radio128px);

    const label128px = document.createElement('label');
    label128px.setAttribute('for', 'canvas-size-128');
    label128px.innerHTML = '128px';
    wrapperRadio.appendChild(label128px);

    radio32px.addEventListener('change', changeCanvas);
    radio64px.addEventListener('change', changeCanvas);
    radio128px.addEventListener('change', changeCanvas);

    document.querySelector('.canvas-field').appendChild(wrapperRadio);
  }

  static renderAdditionalCanvas() {
    const canvasWrapper = document.querySelector('.canvas-wrapper');

    const canvasAdditional = document.createElement('canvas');
    canvasAdditional.setAttribute('id', 'additional-canvas');
    canvasAdditional.setAttribute('width', '32');
    canvasAdditional.setAttribute('height', '32');
    canvasAdditional.addEventListener('contextmenu', e => e.preventDefault());
    canvasAdditional.style.zIndex = '-1';

    canvasWrapper.appendChild(canvasAdditional);
  }

  static renderCoordinatesAndCanvasSize() {
    const coordAndSizeWrapper = document.createElement('div');
    coordAndSizeWrapper.classList.add('coords-size-wrapper');
    document.querySelector('.canvas-field').appendChild(coordAndSizeWrapper);

    const coordinates = document.createElement('p');
    coordinates.classList.add('coordinates');
    coordinates.innerHTML = '[0:0]';
    coordAndSizeWrapper.appendChild(coordinates);

    const canvasSize = document.createElement('p');
    canvasSize.classList.add('canvas-size');
    canvasSize.innerHTML = '[32x32]';
    coordAndSizeWrapper.appendChild(canvasSize);
  }

  static render() {
    CanvasHTMLStructure.renderMainCanvas();
    CanvasHTMLStructure.renderChooseSizeCanvas();
    CanvasHTMLStructure.renderAdditionalCanvas();
    CanvasHTMLStructure.renderCoordinatesAndCanvasSize();
  }
}
