export default class PreviewHTMLStructure {
  static renderPreviewField() {
    const previewField = document.querySelector('.preview-field');

    const animatedPreviewContainer = document.createElement('div');
    animatedPreviewContainer.classList.add('animated-preview-container');
    previewField.appendChild(animatedPreviewContainer);

    const animatedPreviewCanvasContainer = document.createElement('div');
    animatedPreviewCanvasContainer.classList.add('animated-preview-canvas-container');
    animatedPreviewContainer.appendChild(animatedPreviewCanvasContainer);

    const canvasBackground = document.createElement('div');
    canvasBackground.classList.add('canvas-background');
    animatedPreviewCanvasContainer.appendChild(canvasBackground);

    const canvasFrameContainer = document.createElement('canvas');
    canvasFrameContainer.classList.add('canvas-frame-container');
    canvasFrameContainer.setAttribute('width', '32');
    canvasFrameContainer.setAttribute('height', '32');
    animatedPreviewCanvasContainer.appendChild(canvasFrameContainer);
  }

  static renderFPSandFullScreen() {
    const container = document.querySelector('.animated-preview-container');

    const fpsAndFullContainer = document.createElement('div');
    fpsAndFullContainer.classList.add('fps-and-fullscreen-container');
    container.appendChild(fpsAndFullContainer);

    const buttonFullScreen = document.createElement('button');
    buttonFullScreen.classList.add('fullscreen');
    buttonFullScreen.setAttribute('id', 'fullscreen');
    fpsAndFullContainer.appendChild(buttonFullScreen);

    const FPSContainer = document.createElement('div');
    FPSContainer.classList.add('fps-container');
    fpsAndFullContainer.appendChild(FPSContainer);

    const fps = document.createElement('input');
    fps.setAttribute('id', 'speed');
    fps.setAttribute('type', 'range');
    fps.setAttribute('min', '1');
    fps.setAttribute('max', '24');
    fps.setAttribute('step', '1');
    fps.setAttribute('value', '12');
    FPSContainer.appendChild(fps);

    const p = document.createElement('p');
    p.innerHTML = '12 FPS';
    FPSContainer.appendChild(p);
  }

  static render() {
    PreviewHTMLStructure.renderPreviewField();
    PreviewHTMLStructure.renderFPSandFullScreen();
  }
}
