export default class FramesHTMLStructure {
  static renderAddFrame() {
    const addFrameDiv = document.createElement('div');
    addFrameDiv.classList.add('add-frame');
    addFrameDiv.innerHTML = 'Add new frame';
    document.querySelector('.frames-field').appendChild(addFrameDiv);
  }

  static renderFramesList() {
    const framesList = document.createElement('ul');
    framesList.classList.add('frames-list');
    framesList.setAttribute('id', 'frames-list');
    document.querySelector('.frames-field').insertBefore(framesList, document.querySelector('.add-frame'));
  }

  static renderFrame(canvasSize) {
    const li = document.createElement('li');
    li.classList.add('frame');
    li.classList.add('selected-frame');
    document.querySelector('.frames-list').appendChild(li);

    const canvasContainer = document.createElement('div');
    canvasContainer.classList.add('canvas-container');
    li.appendChild(canvasContainer);

    const canvasBackground = document.createElement('div');
    canvasBackground.classList.add('canvas-background');
    canvasContainer.appendChild(canvasBackground);

    const canvas = document.createElement('canvas');
    canvas.classList.add('frame-canvas');
    canvas.setAttribute('width', `${canvasSize || 32}`);
    canvas.setAttribute('height', `${canvasSize || 32}`);
    canvasContainer.appendChild(canvas);

    const buttonDelete = document.createElement('button');
    buttonDelete.classList.add('button-in-frame');
    buttonDelete.classList.add('button-delete');
    li.appendChild(buttonDelete);

    const buttonCopy = document.createElement('button');
    buttonCopy.classList.add('button-in-frame');
    buttonCopy.classList.add('button-copy');
    li.appendChild(buttonCopy);
  }

  static addDisplayNoneForFirstFrame() {
    document.querySelector('.button-delete').style.display = 'none';
  }

  static render() {
    FramesHTMLStructure.renderAddFrame();
    FramesHTMLStructure.renderFramesList();
    FramesHTMLStructure.renderFrame();
    FramesHTMLStructure.addDisplayNoneForFirstFrame();
  }
}
