export default class LandingStructure {
  static landingWrapper() {
    const landingWrapper = document.createElement('div');
    landingWrapper.classList.add('landing-wrapper');
    document.body.appendChild(landingWrapper);
  }

  static sectionHeaderLanding() {
    const section = document.createElement('section');
    section.classList.add('landing-header');
    document.querySelector('.landing-wrapper').appendChild(section);

    const nameApp = document.createElement('h1');
    nameApp.classList.add('landing-header__name');
    nameApp.innerHTML = 'PISKEL CLONE';
    section.appendChild(nameApp);
  }

  static sectionCreateSprite() {
    const section = document.createElement('section');
    section.classList.add('landing-create-sprite');
    document.querySelector('.landing-wrapper').appendChild(section);

    const textWrapper = document.createElement('div');
    textWrapper.classList.add('landing-create-sprite__description-wrapper');
    section.appendChild(textWrapper);

    const description = document.createElement('h2');
    description.classList.add('landing-create-sprite__description');
    description.innerHTML = 'This is a free online editor for animated sprites & pixel art';
    textWrapper.appendChild(description);

    const buttonCreateSprite = document.createElement('button');
    buttonCreateSprite.classList.add('landing-create-sprite__button-create-sprite');
    buttonCreateSprite.innerHTML = 'Create sprite';
    textWrapper.appendChild(buttonCreateSprite);

    const screenshot = document.createElement('div');
    screenshot.classList.add('landing-create-sprite__screenshot');
    section.appendChild(screenshot);
  }

  static sectionExamplesSpritesWrapper() {
    const section = document.createElement('section');
    section.classList.add('landing-sprites-examples');
    document.querySelector('.landing-wrapper').appendChild(section);

    const text = document.createElement('p');
    text.classList.add('landing-sprites-examples__text');
    text.innerHTML = 'Examples sprite';
    section.appendChild(text);

    const firstExample = document.createElement('img');
    firstExample.setAttribute('src', './src/assets/images/landing-page/gif1.gif');
    firstExample.classList.add('landing-sprites-examples__example');
    firstExample.setAttribute('width', '200');
    firstExample.setAttribute('height', '200');
    section.appendChild(firstExample);

    const SecondExample = document.createElement('img');
    SecondExample.setAttribute('src', './src/assets/images/landing-page/gif2.gif');
    SecondExample.classList.add('landing-sprites-examples__example');
    SecondExample.setAttribute('width', '200');
    SecondExample.setAttribute('height', '200');
    section.appendChild(SecondExample);

    const ThirdExample = document.createElement('img');
    ThirdExample.setAttribute('src', './src/assets/images/landing-page/gif3.gif');
    ThirdExample.classList.add('landing-sprites-examples__example');
    ThirdExample.setAttribute('width', '200');
    ThirdExample.setAttribute('height', '200');
    section.appendChild(ThirdExample);

    const FourthExample = document.createElement('img');
    FourthExample.setAttribute('src', './src/assets/images/landing-page/gif4.gif');
    FourthExample.classList.add('landing-sprites-examples__example');
    FourthExample.setAttribute('width', '200');
    FourthExample.setAttribute('height', '200');
    section.appendChild(FourthExample);
  }

  static sectionImplementedFunctionality() {
    const section = document.createElement('section');
    section.classList.add('landing-overfiew-functionality');
    document.querySelector('.landing-wrapper').appendChild(section);

    const ul = document.createElement('ul');
    ul.classList.add('landing-overfiew-functionality__tools-container');
    section.appendChild(ul);

    const firstTool = LandingStructure.renderOneTool('Live preview', 'Check a preview of your animation in real time as you draw. Adjust the frame delay on the fly.');
    ul.appendChild(firstTool);

    const secondTool = LandingStructure.renderOneTool('Google Sign in', 'No need to remember yet another password, just use your Google account to sign in.');
    ul.appendChild(secondTool);

    const thirdTool = LandingStructure.renderOneTool('Export to GIF, APNG', 'Export your animated sprite to file system in .gif or .apng format.');
    ul.appendChild(thirdTool);

    const fourthTool = LandingStructure.renderOneTool('A lot of diffrent tools', 'The editor has 10 and more diffrent tools for your creative.');
    ul.appendChild(fourthTool);
  }

  static renderOneTool(title, description) {
    const li = document.createElement('li');
    li.classList.add('landing-overfiew-functionality__tools-container__tool');

    const titleLi = document.createElement('h4');
    titleLi.innerHTML = title;
    li.appendChild(titleLi);

    const descriptionP = document.createElement('p');
    descriptionP.innerHTML = description;
    li.appendChild(descriptionP);

    return li;
  }

  static sectionInfoAboutAuthor() {
    const section = document.createElement('section');
    section.classList.add('landing-information-about-author');
    document.querySelector('.landing-wrapper').appendChild(section);

    const title = document.createElement('h2');
    title.classList.add('landing-information-about-author__title');
    title.innerHTML = 'CONTACTS';
    section.appendChild(title);

    const text = document.createElement('p');
    text.classList.add('landing-information-about-author__text');
    text.innerHTML = 'App developed by Vladislav Shestakov';
    section.appendChild(text);

    const linkContainer = document.createElement('div');
    linkContainer.classList.add('landing-information-about-author__link-container');
    section.appendChild(linkContainer);

    const linkGithub = document.createElement('a');
    linkGithub.classList.add('landing-information-about-author__github');
    linkGithub.setAttribute('href', 'https://github.com/Shestak2039');
    linkGithub.setAttribute('target', '_blank');
    linkContainer.appendChild(linkGithub);

    const imgGithub = document.createElement('img');
    imgGithub.classList.add('landing-information-about-author__github_image');
    imgGithub.setAttribute('src', './src/assets/images/landing-page/GitHub-Mark-64px.png');
    linkGithub.appendChild(imgGithub);

    const linkTelegram = document.createElement('a');
    linkTelegram.classList.add('landing-information-about-author__telegram');
    linkTelegram.setAttribute('href', 'https://t.me/shestak2039');
    linkTelegram.setAttribute('target', '_blank');
    linkContainer.appendChild(linkTelegram);

    const imgTelegram = document.createElement('img');
    imgTelegram.classList.add('landing-information-about-author__telegram_image');
    imgTelegram.setAttribute('src', './src/assets/images/landing-page/telegram-logo.png');
    linkTelegram.appendChild(imgTelegram);
  }

  static render() {
    LandingStructure.landingWrapper();
    LandingStructure.sectionHeaderLanding();
    LandingStructure.sectionCreateSprite();
    LandingStructure.sectionExamplesSpritesWrapper();
    LandingStructure.sectionImplementedFunctionality();
    LandingStructure.sectionInfoAboutAuthor();
  }
}
