import download from 'downloadjs';
import GIFEncoder from 'gif-encoder-2';
import getCurrentFPS from '../preview/getCurrentFPS';
import getCanvasSize from '../canvasSize/getCanvasSize';

export default function exportGIFtoLocalSystem() {
  const framesCanvas = Array.from(document.querySelectorAll('.frame-canvas'));

  const encoder = new GIFEncoder(getCanvasSize(), getCanvasSize());
  encoder.setDelay(1000 / getCurrentFPS());
  encoder.start();

  for (let i = 0; i < framesCanvas.length; i += 1) {
    encoder.addFrame(framesCanvas[i].getContext('2d'));
  }

  encoder.finish();

  const buffer = encoder.out.getData();
  const b64encoded = btoa(String.fromCharCode.apply(null, buffer));
  const datajpg = `data:image/jpg;base64,${b64encoded}`;

  download(datajpg, 'gif.gif', 'image/gif');
}
