import download from 'downloadjs';
import getCurrentFPS from '../preview/getCurrentFPS';
import getCanvasSize from '../canvasSize/getCanvasSize';

function doCanvasLayout() {
  const frames = document.querySelectorAll('.frame-canvas');

  const canvas = document.createElement('canvas');
  canvas.setAttribute('width', getCanvasSize() * frames.length);
  canvas.setAttribute('height', getCanvasSize());
  const ctx = canvas.getContext('2d');

  for (let i = 0; i < frames.length; i += 1) {
    const picture = frames[i].getContext('2d').getImageData(0, 0, getCanvasSize(), getCanvasSize());
    ctx.putImageData(picture, getCanvasSize() * i, 0);
  }
  document.body.appendChild(canvas);

  return canvas.toDataURL();
}

function collectFile() {
  const piskelFile = {};
  const frames = document.querySelectorAll('.frame-canvas');

  piskelFile.modelVersion = 2;
  piskelFile.piskel = {};
  piskelFile.piskel.name = 'New Piskel';
  piskelFile.piskel.description = '';
  piskelFile.piskel.fps = getCurrentFPS();
  piskelFile.piskel.height = getCanvasSize();
  piskelFile.piskel.width = getCanvasSize();

  let layerLayout = '';
  for (let i = 0; i < frames.length; i += 1) {
    if (i === 0) {
      layerLayout += '[[0],';
    } else if (i === frames.length - 1) {
      layerLayout += `[${i}]]`;
    } else {
      layerLayout += `[${i}],`;
    }
  }

  // eslint-disable-next-line no-useless-escape
  piskelFile.piskel.layers = [`{\"name\":\"Layer 1\",\"opacity\":1,\"frameCount\":${frames.length},\"chunks\":[{\"layout\":${layerLayout},\"base64PNG\":\"${doCanvasLayout()}\"}]}`];
  piskelFile.piskel.hiddenFrames = [];

  return JSON.stringify(piskelFile);
}

export default function exportToPiskel() {
  download(collectFile(), 'new Piskel.piskel', 'json');
}
