import getCanvasSize from '../canvasSize/getCanvasSize';

function getCurrentFrames() {
  return document.querySelectorAll('.frame-canvas');
}

export default function animation() {
  let frames = getCurrentFrames();
  let i = 0;
  const canvasContainer = document.querySelector('.canvas-frame-container');

  return () => {
    if (frames.length !== getCurrentFrames().length || frames.length === i) {
      frames = getCurrentFrames();
      i = 0;
    }
    const picture = frames[i].getContext('2d').getImageData(0, 0, getCanvasSize(), getCanvasSize());
    canvasContainer.getContext('2d').putImageData(picture, 0, 0);
    i += 1;
  };
}
