import eventForApper from '../modal/eventForAppear';
import eventForDisapper from '../modal/eventForDisapper';
import { eventForChangeShortcut, eventForClickShortcut } from '../modal/eventForChangeShortcut';

export default function ModalController() {
  document.querySelector('.hotkeys').addEventListener('click', eventForApper);
  document.querySelector('.modal__cross').addEventListener('click', eventForDisapper);
  document.querySelector('.shortcut-container').addEventListener('click', eventForClickShortcut);
  document.addEventListener('keydown', eventForChangeShortcut);
}
