import hexToRgba from './utils/hexToRgba';
import getCanvasSize from '../canvasSize/getCanvasSize';
import setPixel from './utils/setPixel';

export default function paintAllPixelsOfSameColor(e) {
  let rgbColor;
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');
  const x = Math.floor(e.offsetX * getCanvasSize() / canvas.offsetWidth);
  const y = Math.floor(e.offsetY * getCanvasSize() / canvas.offsetHeight);
  const pixel = ctx.getImageData(x, y, 1, 1);

  if (e.which === 1 || e.which === 3) {
    if (e.which === 1) {
      rgbColor = hexToRgba(document.getElementById('primary-color').value, 255);
    } else if (e.which === 3) {
      rgbColor = hexToRgba(document.getElementById('secondary-color').value, 255);
    }

    for (let x1 = 0; x1 < getCanvasSize(); x1 += 1) {
      for (let y1 = 0; y1 < getCanvasSize(); y1 += 1) {
        const pixelOnCanvas = ctx.getImageData(x1, y1, 1, 1);
        if (pixel.data[0] === pixelOnCanvas.data[0] && pixel.data[1] === pixelOnCanvas.data[1]
          && pixel.data[2] === pixelOnCanvas.data[2] && pixel.data[3] === pixelOnCanvas.data[3]) {
          setPixel(rgbColor, x1, y1, ctx);
        }
      }
    }
  }
}
