import rgbToHex from './utils/rgbToHex';
import getCanvasSize from '../canvasSize/getCanvasSize';

export default function colorPicker(e) {
  const canvas = document.getElementById('canvas');
  const ctx = canvas.getContext('2d');
  const x = Math.floor(e.offsetX * getCanvasSize() / canvas.offsetWidth);
  const y = Math.floor(e.offsetY * getCanvasSize() / canvas.offsetHeight);

  const pixel = ctx.getImageData(x, y, 1, 1);

  const red = pixel.data[0];
  const green = pixel.data[1];
  const blue = pixel.data[2];

  const hexColor = rgbToHex(red, green, blue);

  if (e.which === 1 || e.which === 3) {
    if (e.which === 1) {
      document.getElementById('primary-color').value = hexColor;
      document.getElementById('primary-color').setAttribute('value', hexColor);
    } else if (e.which === 3) {
      document.getElementById('secondary-color').value = hexColor;
      document.getElementById('secondary-color').setAttribute('value', hexColor);
    }
  }
}
