export default class BasicHTMLStructure {
  static render() {
    const header = document.createElement('header');
    header.classList.add('header');
    document.body.appendChild(header);

    const wrapper = document.createElement('div');
    wrapper.classList.add('wrapper');

    const tools = document.createElement('div');
    tools.classList.add('tools');
    wrapper.appendChild(tools);

    const framesField = document.createElement('div');
    framesField.classList.add('frames-field');
    wrapper.appendChild(framesField);

    const canvasField = document.createElement('div');
    canvasField.classList.add('canvas-field');
    wrapper.appendChild(canvasField);

    const previewField = document.createElement('div');
    previewField.classList.add('preview-field');
    wrapper.appendChild(previewField);

    const exportField = document.createElement('div');
    exportField.classList.add('export-field');
    wrapper.appendChild(exportField);

    document.body.appendChild(wrapper);
  }
}
