import Sortable from 'sortablejs';
import InteractionCanvasAndFrame from '../frames/InteractionWithOtherComponents/InteractionCanvasAndFrame';
import EventsFrames from '../frames/EventsFrame';

export default function framesController() {
  document.querySelector('.add-frame').addEventListener('click', InteractionCanvasAndFrame.eventAddNewFrame);
  document.querySelector('.frames-list').addEventListener('click', InteractionCanvasAndFrame.eventClickOnFrame);
  document.querySelector('.frames-list').addEventListener('click', EventsFrames.eventDeleteFrame);
  document.querySelector('.frames-list').addEventListener('click', EventsFrames.eventCopyFrame);
  document.querySelector('.frames-list').addEventListener('mousedown', EventsFrames.eventMoveFrame);
  Sortable.create(document.querySelector('.frames-list'), {});
}
