import InteractionCanvasAndFrame from '../frames/InteractionWithOtherComponents/InteractionCanvasAndFrame';
import showCoordinates from '../canvas/showCoordinates';

export default function CanvasController() {
  document.querySelector('.canvas-wrapper').addEventListener('mouseup', InteractionCanvasAndFrame.eventMouseUpCanvas);
  document.querySelector('.canvas-wrapper').addEventListener('mouseleave', InteractionCanvasAndFrame.eventMouseUpCanvas);
  document.querySelector('.canvas-wrapper').addEventListener('mousemove', showCoordinates);
}
